<template>
      <div class="item " >
          <i class="complete_icon iconfont" @click="action('toggle_complete',todo.id)">&#xe631;</i>
        {{todo.title}}
      
             <div class="btn-r">
                    <span @click="action('set_current_show_detail',todo)"><i class="iconfont">&#xe60d;</i></span>
                    <span @click="action('remove',todo.id,$event)"><i class="iconfont">&#xe60c;</i></span>
                    <span @click="action('toggle_detail',todo.id)"><i class="iconfont">&#xe64d;</i></span>
             </div>
          <div class="detail" v-if="todo.show_desc">{{todo.desc || '暂无详情'}} </div>
    
      </div>
</template>

<script>
import Event from "../eventBus"
export default {
    props:["todo"],
    methods:{
        action(name,params,$event){
            Event.$emit(name,params);
        }
    }
}
</script>

