export default {
    set:function(key,value){
        value=JSON.stringify(value);//提前变为字符串，以防是对象时被浏览器存储为'[object object]'

        localStorage.setItem(key,value)
    },
     get :function(key){
         const json = localStorage.getItem(key);
         if(json){
             return JSON.parse(json);
         }
    }
}
   
