<template>
  <div class="wrapper">
    <audio controls id="alert_sound">
      <source src="./assets/alert.mp3">
    </audio>
    <div class="navbar">
      <h1>To Do List</h1>
    </div>
    <div class="header">
      <h3>
        输入内容+
        <i class="iconfont icon-huiche">&#xe651;</i>
      </h3>
    </div>

    <form id="task-form" @submit.prevent="merge">
      <div class="container">
        <div class="input-box">
          <input type="text" v-model="current.title">
          <div class="control">
            <i class="iconfont" @click="merge">&#xe618;</i>
            <i class="iconfont" @click="desc_show = !desc_show">&#xe64d;</i>
          </div>
        </div>
        <div class="desc" v-if="desc_show">
          <span class="desc_title">详情描述</span>
          <textarea v-model="current.desc"></textarea>
          <span class="desc_title">定时提醒</span>
          <input type="datetime-local" v-model="current.time">
        </div>
      </div>
    </form>

    <div class="task-list">
      <div class="container">
        <span class="task-title">待办事项</span>
        <!-- 一种是用组件写 -->
        <transition-group
          enter-active-class="animated fadeIn"
          leave-active-class="animated fadeOut"
        >
          <task v-for="todo in list" :key="todo.id" v-if="!todo.completed" :todo="todo"></task>
        </transition-group>
      </div>
    </div>
    <div class="task-list">
      <div class="container">
        <!-- 一种是直接写 -->
        <span class="task-title">完成事项</span>
        <transition-group
          enter-active-class="animated fadeIn"
          leave-active-class="animated fadeOut"
        >
          <div class="item completed" v-for="todo in list" v-if="todo.completed" :key="todo.id">
            <i class="uncomplete_icon iconfont" @click="toggle_complete(todo.id)">&#xe632;</i>
            {{todo.title}}
            <div class="btn-r">
              <span @click="set_current(todo)">
                <i class="iconfont">&#xe60d;</i>
              </span>
              <span @click="remove(todo.id)">
                <i class="iconfont">&#xe60c;</i>
              </span>
            </div>
          </div>
        </transition-group>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import ms from "./js/myStorage.js";
import task from "./components/task";
import Event from "./eventBus";
import { setInterval } from "timers";
// import {set,get} from "./js/myStorage.js"

function copy(obj) {
  return Object.assign({}, obj);
}
export default {
  name: "app",
  data() {
    return {
      list:[],
      _list: [
        {
          id: 0,
          title: "示例",
          desc: "可输入标题、描述，还能设置定时提醒哦",
          show_desc: true
        }
      ],
      current: {},
      last_id: 0, //为了确保下一个id是对的
      desc_show: false,
    };
  },
  mounted() {
    // 从localstorage获取数据
    const $list = ms.get("list");
    
    if(!$list || $list.length===0){
      const   _list= [
        {
          id: 0,
          title: "示例",
          desc: "可输入标题、描述，还能设置定时提醒哦",
          show_desc: true
        }
      ];
      ms.set('list',_list);
    }
    this.list = ms.get("list");
    this.last_id = ms.get("last_id") || this.last_id;

    // 绑定事件
    Event.$on("remove", id => {
      this.remove(id);
    });
    Event.$on("toggle_complete", id => {
      this.toggle_complete(id);
    });
    Event.$on("set_current_show_detail", obj => {
      this.set_current_show_detail(obj);
    });
    Event.$on("toggle_detail", id => {
      this.toggle_detail(id);
    });
    // 事件提醒,轮询
    setInterval(() => {
      this.check_alert();
    }, 1000);
  },
  watch: {
    list: {
      deep: true, //不管list嵌套多深都会监测到
      handler: function(n) {
        
        //参数有：new,old
        //new ,old
        if (n) {
          // 更新localStorage
          ms.set("list", n);
        } else {
          ms.set("list", {});
        }
      }
    }
  },
  components: {
    task
  },
  methods: {
    merge() {
      //添加或者更新  索引是动态的不可依赖

      const is_update = this.current.id;
      if (is_update || is_update == 0) {
        // 有id,说明是更新
        // const target = this.list.find(el => {
        //   return el.id === is_update;
        // });
        // target.title = this.current.title;

        const index = this.list.findIndex(item => item.id == is_update);

        // 数组的元素修改，不能直接修改，需要用Vue.set(...)这段代码表严肃写之后vue没有检测到更新，但是我这里是可以的
        Vue.set(this.list, index, copy(this.current)); //这样就能检测到啦！
        // this.list[index] = copy(this.current);
      } else {
        const title = this.current.title;

        if (!title && title !== 0) return; //为空处理
        const obj = copy(this.current); //this.current是引用，为了不影响其他item需要重新拷贝一份

        obj.id = ++this.last_id;
        ms.set("last_id", this.last_id);
        obj.completed = false; //默认未完成状态

        this.list.push(obj);
      }
      this.reset_current();
    },
    remove(id) {
      //用索引来删除id是不行的。
      this.list.splice(this.list.findIndex(el => el.id === id), 1);
      // 更新localStorage
    },
    toggle_complete(id) {
      const idx = this.find_index(id);
      // this.list[idx].completed = !this.list[idx].completed;
      // 上面也有效，但以防万一，写成下面的形式
      Vue.set(this.list[idx], "completed", !this.list[idx].completed);
    },
    set_current_show_detail(todo) {
      this.set_current(todo);
      this.desc_show = true;
    },
    set_current(todo) {
      this.current = copy(todo);
    },
    reset_current() {
      this.set_current({});
    },
    find_index(id) {
      return this.list.findIndex(el => el.id == id);
    },
    toggle_detail(id) {
      const index = this.find_index(id);
      Vue.set(this.list[index], "show_desc", !this.list[index].show_desc);
    },
    check_alert() {
      this.list.forEach((el, i) => {
        if (!el.time || el.alert_comfirmed) return;
        const alert_at = new Date(el.time).getTime();
        const now = new Date().getTime();
        // 获取当前时间戳

        if (alert_at <= now) {
          const alert_sound = document.getElementById("alert_sound");
          alert_sound.play();
          if (window.confirm(el.title)) {
            Vue.set(this.list[i], "alert_comfirmed", true);
            // clearInterval();
          }
        }
      });
    }
  }
};
</script>

<style lang="scss" src="./css/todo.scss">
</style>
